<template>
  <div class="amountListResult">
    <v-container
      fluid
      grid-list-xl
    >
      <div class="">
        <p class="category font-weight-light">帳務日期：{{ count_date }} 至 {{ count_date_end }}</p>
      </div>

      <div class="pt-1">
        <v-data-table
          :headers="tableData.headers"
          :items="tableData.list"
          :no-data-text="tableData.showNoData"
          :dark="false"
          :default-sort="true"
          class="elevation-1"
          hide-actions
        >
          <template
            slot="items"
            slot-scope="{ item }">
            <td class="text-xs-center">{{ item.m_id }}</td>
            <td class="text-xs-center">{{ item.m_date }}</td>
            <td class="text-xs-center">{{ item.bet_gold }}</td>
            <td class="text-xs-center">{{ item.ip }}</td>
            <td class="text-xs-center">{{ item.gold }}</td>
            <td class="text-xs-center">{{ item.cNote }}</td>
          </template>
        </v-data-table>
        <v-btn
          small
          color="deep-orange darken-1"
          @click="toBack()">返回</v-btn>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
// import $ from 'jquery'
export default {
  name: 'AmountListResult',
  data () {
    return {
      tableData: {
        showNoData: '目前無任何資料',
        headers: [
          { text: '帳號', align: 'center', sortable: false },
          { text: '時間', align: 'center', sortable: false },
          { text: '項目', align: 'center', sortable: false },
          { text: 'IP', align: 'center', sortable: false },
          { text: '金額', align: 'center', sortable: false },
          { text: '備註', align: 'center', sortable: false }
        ],
        list: []
      },
      count_date: this.$route.query.count_date,
      count_date_end: this.$route.query.count_date_end
    }
  },
  computed: {
    ...mapGetters({
      getQuotaListState: 'report/getQuotaListState'
    })
  },
  watch: {
    getQuotaListState (payload) {
      console.log(payload)
      this.tableData.list = payload.result
      console.log(payload.result)
    }
  },
  mounted () {
    this.loadQuotaList()
  },
  methods: {
    ...mapActions({
      getQuotaList: 'api/getQuotaList'
    }),
    // 載入帳務紀錄搜尋結果資料
    loadQuotaList (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        count_date: this.$route.query.count_date,
        count_date_end: this.$route.query.count_date_end
      }
      this.getQuotaList(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    toBack () {
      this.$router.push({
        path: '/manage/report/amout-list'
      })
    }
  }
}
</script>

<style lang="scss">
</style>
